import { Injectable } from '@angular/core';
import { HttpClient } from '@angular/common/http';
import { ConfigService } from '../config/config.service';
import { Observable } from 'rxjs';
import { Pokemon } from '../public-api';

@Injectable({
  providedIn: 'root',
})
export class ApiService {
  private readonly apiBase: string = this.configS.apiUrl;
  constructor(private http: HttpClient, private configS: ConfigService) {}

  getAllDataPokemon(): Observable<Pokemon> {
    if (this.configS.optionAPIGW) {
      return this.http.get<Pokemon>(`${this.apiBase}/pokemon/pikachu`);
    } else {
      return this.http.get<Pokemon>(`${this.apiBase}/pokemon/charmander`);
    }
  }
}
