import { Inject, Injectable, Optional } from '@angular/core';
import { UI_KIT_CONFIG } from './token-config';
import { UiKitConfig } from './config.interface';

@Injectable({
  providedIn: 'root',
})
export class ConfigService {
  constructor(
    @Optional() @Inject(UI_KIT_CONFIG) private config?: UiKitConfig
  ) {}

  logConfig() {
    console.log('Config de la librer√≠a:', this.config);
  }

  get apiUrl(): string {
    return this.config?.apiUrl ?? 'https://pokeapi.co/api/v2';
  }

  get optionAPIGW(): boolean {
    return this.config?.apigw ?? false;
  }
}
